.banner_belt .inner {
  position: relative;
  margin: 0 auto;
  width: 1200px;
}

.banner_belt .inner .close {
  position: absolute;
  right: 0;
  top: 50%;
  width: 20px;
  height: 20px;
  background: url(../../common/images/btn_tooltip_close.png) no-repeat center;
  transform: translate(30px, -50%);
  text-indent: -1000px;
  overflow: hidden;
  z-index: 1;
  cursor: pointer;
}

.banner_toast {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  visibility: hidden;
  transform: translate(0, 174px);
  transition: 0.5s ease-out all;
  z-index: 1000;
}

.banner_toast.view {
  visibility: visible;
  transform: translate(0, 0);
}

.banner_toast .inner {
  position: relative;
  margin: 0 auto;
  width: 1200px;
}

.banner_toast .inner .close {
  position: absolute;
  right: 32px;
  top: 32px;
  width: 24px;
  height: 24px;
  background: url(../../common/images/btn_dialog_close_w.png) no-repeat center;
  background-size: contain;
  text-indent: -1000px;
  overflow: hidden;
  z-index: 1;
  cursor: pointer;
}

.banner_toast .inner .form_chk {
  position: absolute;
  right: 32px;
  top: -34px;
  z-index: 1;
}

.banner_toast .inner .form_chk label {
  font-size: 13px;
  color: #595959;
}

.banner_fly_left {
